// ==========================================================================
// Form Elements
// ==========================================================================

.form {
	@include e(label) {
		@include m(hidden) {
			@extend .srt;
		}
		display: block;
		margin-bottom: em(10px);
	}

	@include e(checkboxGroup) {
		text-transform: uppercase;
		color:$white;
		margin-bottom: em(20px);
		label {
			display: flex;
			align-items:center;
			@include mq(small) {
				font-size: em(12px);
			}
		}

	}	

	@include e(checkboxInput) {
		display:none;
		&:checked + label .form__customCheckbox {
			@include img-replace("check.png", 25px, 25px, inline-block, center, 15px 15px);
		}
	}

	@include e(customCheckbox) {
		
		margin-right:10px; 
		display:inline-block;
		width:25px;
		height:25px;
		border-radius: 5px;
		background-color: $white;
		cursor:pointer;
	}
		
			

	@include e(input) {
		font-size: em(18px);
		margin-bottom: em(20px, 18px);
		border-bottom: 6px solid palette(grey, x-light);
		border-radius: $br--default;
		background: palette(grey, xx-light);
		color: palette(black, dark);
		font-weight: $font-weight--light;

		&:focus {	
			border-color: $color-primary;
		}

		
	}

	@include e(btn) {
		@extend %btn;
		background: $color-primary;
	}
}



.contact-form {
	@include e(group) {
		display:flex;
		align-items:center;
		justify-content: flex-end;

		@include m(button) {
			display:flex;
			align-items:center;
			justify-content: flex-start;
			margin-left: em(100px);
			@include mq(small) {
			  margin-left: 0;
			  margin-top:10px;
			  justify-content: center;
			}
		}

		@include mq(small) {
			flex-wrap:wrap;
			justify-content: center;
		}
	}

	@include e(input) {
		max-width:450px;
		width:100%;
		display:inline-block;
		margin:20px;
		border-radius:$br--default;
		@include mq(small) {
			margin:1px;
		}
	}

	@include e(textarea) {
		margin:20px;
		max-width:450px;
		display:inline-block;
		height:85px;
		border-radius:$br--default;
		@include mq(small) {
			margin:1px;
		}
	}

	@include e(label) {
		text-align:right;
		color:$white;
		display:inline-block;
		text-transform: capitalize;
		@include mq(small) {
			display:none;
		}
	}


	@include mq(small) {
			flex-wrap:wrap;
			justify-content: center;
			margin-top: em(100px);
	}


}