// ==========================================================================
// Grid Columns
// ==========================================================================

// Calculate grid columns

.grid__col {
  @include mq(medium) {
    @include g-columns; 
  }
}

// Column styles

[class^="grid__col--"] {
  position:relative;
  @include mq(small)  {
    margin-top: em(12px);
    margin-bottom: em(12px);
  }
  @include mq(medium) {
    @include doubly(percentage($g-gutter-width / $context));    
    float: left;
    min-height: 1px;
    &:last-of-type {
      float: right;
    }
  }
}